import { writable } from 'svelte/store';
import { THARGOID_TYPES } from './data/thargoidData';
import { MAX_AX_WEAPONS } from './util';
const buildStarterWeaponStore = () => {
    const weapons = {};
    for (let i = 0; i < MAX_AX_WEAPONS; i++) {
        weapons[i] = { name: '', class: undefined };
    }
    return weapons;
};
export const setEmptyTotStore = () => {
    const totData = {};
    for (let type of THARGOID_TYPES) {
        totData[type.name] = {
            basicAmmo: { adjDps: 0, tot100: 'N/A', tot75: 'N/A', tot50: 'N/A' },
            standardAmmo: { adjDps: 0, tot100: 'N/A', tot75: 'N/A', tot50: 'N/A' },
            premiumAmmo: { adjDps: 0, tot100: 'N/A', tot75: 'N/A', tot50: 'N/A' }
        };
    }
    return totData;
};
// Input stores
export const selectedWeapons = writable(buildStarterWeaponStore());
export const range = writable(1500);
export const selectedDistributor = writable({ size: 1, class: 'A', blueprint: '', experimentEffect: '' });
export const heatsinks = writable(0);
// Calculation stores
export const sdpsExtraDelay = writable(0);
export const timeOnTargetData = writable(setEmptyTotStore());
